body {
  height: 100vh;
  font-family: Roboto, sans-serif;
}

.split-viewer {
  height: inherit;
  width: inherit;
  justify-content: space-between;
  display: flex;
}

.split-viewer, .split-viewer *, .split-viewer :before, .split-viewer :after {
  box-sizing: border-box;
}

.split-viewer__section {
  width: 50%;
  background-position: center;
  background-size: cover;
  transition: all 1.5s;
  position: relative;
}

.split-viewer__section:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
}

.split-viewer__section_left {
  background-image: url("ps.da081c0f.jpg");
}

.split-viewer__section_left:before {
  background-color: #5754ecb3;
}

.split-viewer__section_right {
  background-image: url("xbox.0de2f868.jpg");
}

.split-viewer__section_right:before {
  background-color: #2b2b2bcc;
}

.split-viewer__section_active {
  width: 90%;
}

.split-viewer__content {
  width: 100%;
  height: 100%;
  z-index: 20;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.split-viewer__title {
  color: #fff;
  text-align: center;
  white-space: nowrap;
  margin: 0 0 30px;
  font-size: 70px;
}

.split-viewer__btn {
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  cursor: pointer;
  background-color: #0000;
  border: 3px solid #fff;
  border-radius: 3px;
  padding: 20px 60px;
  font-size: 17px;
  font-weight: bold;
}

.split-viewer__btn_blue:hover {
  background-color: #4c4cf9;
  border-color: #4c4cf9;
}

.split-viewer__btn_green:hover {
  background-color: #089938;
  border-color: #089938;
}

