
body {
    font-family: 'Roboto', sans-serif;
    height: 100vh;
}

$color_white: #fff;
$color_blue: rgb(76, 76, 249);
$color_green:  rgb(8, 153, 56);
$backgroud_violet: rgba(87, 84, 236, 0.7);
$backgroud_gray: rgba(43, 43, 43, 0.8);
$transition_time: 1.5s;

.split-viewer {  
    &,
    & *,
    & *:before,
    & *:after {
        box-sizing: border-box;
    }

    display: flex;
    justify-content: space-between;

    height: inherit;
    width: inherit;

    &__section {
        position: relative;
    
        width: 50%;
    
        background-size: cover;
        background-position: center;
    
        transition: $transition_time;
    }

    &__section::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
    }
    
    &__section_left {
        background-image: url(img/ps.jpg);
    }
    
    &__section_left::before {
        background-color: $backgroud_violet;
    }
    
    &__section_right {
        background-image: url(img/xbox.jpg);
    }
    
    &__section_right::before {
        background-color: $backgroud_gray;
    }
    
    &__section_active {
        width: 90%;
    }
    
    &__content {    
        position: absolute;
    
        padding: 20px;
    
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    
        width: 100%;
        height: 100%;
    
        z-index: 20;
        overflow: hidden;
    }
    
    &__title {
        margin: 0;
        margin-bottom: 30px;
    
        color: $color_white;
        font-size: 70px;
        text-align: center;
        white-space: nowrap;
    }
    
    &__btn {
        padding: 20px 60px;
    
        color: $color_white;
        font-weight: bold;
        font-size: 17px;
        text-transform: uppercase;
        white-space: nowrap;
    
        border: 3px solid $color_white;
        border-radius: 3px;
        background-color: transparent;
        cursor: pointer;
    }
    
    &__btn_blue:hover {
        background-color: $color_blue;
        border-color: $color_blue;
    }
    
    &__btn_green:hover {
        background-color: $color_green;
        border-color: $color_green;
    }
}

